/* === Base Styles === */
.app-root {
  min-height: 100vh;
  color: #fff;
  display: flex;
  flex-direction: column;
  margin: 0;
  padding: 0;
  font-family: 'Inter', sans-serif;
  position: relative;
}

/* === Search Bar Wrapper === */
.search-wrapper {
  position: absolute;
  top: 3rem;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  align-items: center;
  gap: 10px;
}

/* === Search Form (collapsible) === */
.search-form {
  display: flex;
  align-items: center;
  overflow: hidden;
  width: 20rem;
  transition:
    width 0.3s ease-in-out,
    opacity 0.3s ease-in-out;
}
.search-form.hidden {
  width: 0;
  opacity: 0;
}
.search-form.visible {
  width: 20rem;
  opacity: 1;
}

.search-input {
  height: 3.5rem;
  width: 100%;
  font-size: 1.2rem;
  font-weight: 600;
  padding: 0 1.5rem;
  color: #9a9a9a;
  background: rgba(86, 86, 86, 0.1);
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
  box-shadow: 0 4px 30px rgba(0, 0, 0, 0.2);
  border: none;
  border-radius: 1rem;
  transition: opacity 0.3s ease-in-out;
}
.search-input:focus {
  opacity: 0.8;
  outline: none;
}

/* === Hide/Show Button === */
.hide-search-button {
  position: relative;
  height: 3.5rem;
  font-size: 1rem;
  font-weight: 600;
  color: #9a9a9a;
  background: rgba(86, 86, 86, 0.1);
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
  border: none;
  border-radius: 1rem;
  cursor: pointer;
  display: flex;
  align-items: center;
  transition:
    background 0.2s ease-in-out,
    top 0.3s ease-in-out,
    left 0.3s ease-in-out;
  z-index: 1000;
}
.hide-search-button:hover {
  background: rgba(86, 86, 86, 0.2);
}
.hide-search-button.transformed {
  position: fixed;
  top: 0rem;
  left: -75rem;
}
.hide-search-button:not(.transformed) {
  position: relative;
  top: 0;
  left: 0;
}

/* === Widget Grid === */
.widgets {
  flex: 1;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(600px, 600px));
  justify-content: center;
  gap: 10rem;
  width: 100%;
  padding: 2rem;
  box-sizing: border-box;
  align-content: center;
}
.widget {
  width: 100%;
  min-height: 620px;
  display: flex;
  justify-content: center;
  align-items: center;
}

/* === Now Playing Widget === */
.now-playing-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  max-width: 600px;
  margin: 0 auto;
  min-height: 620px;
  transition: all 0.3s ease-in-out;
}
.album-art-wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 30px;
  padding: 10px;
}
.album-art-bg {
  width: 600px;
  height: 600px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 20px;
  overflow: hidden;
  position: relative;
}
.album-art {
  width: 100%;
  height: 100%;
  min-height: 300px;
  object-fit: cover;
  border-radius: 20px;
}
.track-info {
  display: flex;
  flex-direction: column;
  text-align: left;
  width: 600px;
  margin-top: 10px;
}
.track-name {
  font-size: 2.5rem;
  font-weight: bold;
  color: rgba(188, 188, 188, 0.8);
  margin-bottom: 10px;
}
.track-artists {
  font-size: 1.7rem;
  color: rgba(188, 188, 188, 0.8);
}
.nothing-playing {
  display: inline-block;
  font-size: 1.2rem;
  font-weight: 600;
  color: #9a9a9a;
  padding: 0.75rem 1.5rem;
  border-radius: 1rem;
  background: rgba(86, 86, 86, 0.1);
  backdrop-filter: blur(10px);
  box-shadow: 0 4px 30px rgba(0, 0, 0, 0.2);
  margin: 0 auto;
}

/* === Calendar Widget Container === */
.calendar-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
}

/* === Utility Classes === */
.hidden {
  display: none !important;
}
.login-button {
  display: block;
  font-size: 1rem;
  font-weight: 600;
  color: #9a9a9a;
  background: rgba(86, 86, 86, 0.1);
  padding: 1rem 2rem;
  border: none;
  outline: none;
  border-radius: 1rem;
  box-shadow: 0 4px 30px rgba(0, 0, 0, 0.2);
  cursor: pointer;
  max-width: 30%;
  margin: 15rem auto;
  text-align: center;
}

/* === To-Do List === */
.todo-container {
  width: 300px;
  max-width: 600px;
  height: 620px;
  overflow-y: auto;
  padding: 1rem 2rem;
  background: rgba(86, 86, 86, 0.244);
  border-radius: 1rem;
  box-shadow: 0 4px 30px rgba(0, 0, 0, 0.2);
  display: flex;
  flex-direction: column;
}
.todo-title {
  font-size: 2rem;
  font-weight: bold;
  color: rgba(255, 255, 255, 0.85);
  margin-bottom: 1rem;
}
.todo-input {
  padding: 0.75rem 1rem;
  background: rgba(255, 255, 255, 0.1);
  color: #fff;
  border: none;
  border-radius: 0.75rem;
  margin-bottom: 1rem;
}
.todo-button {
  padding: 0.75rem 1rem;
  background: rgba(255, 255, 255, 0.15);
  color: #fff;
  border: none;
  border-radius: 0.75rem;
  cursor: pointer;
}
.todo-button:hover {
  background: rgba(255, 255, 255, 0.25);
}
.todo-list {
  list-style: none;
  padding: 0;
  margin-top: 1rem;
  flex: 1;
  overflow-y: auto;
}
.todo-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: rgba(255, 255, 255, 0.05);
  padding: 0.5rem 1rem;
  border-radius: 0.75rem;
  margin-top: 0.5rem;
  word-break: break-word;
}
.todo-item button {
  background: none;
  border: none;
  color: #f87171;
  cursor: pointer;
}

/* === Revision Calendar Wrapper === */
.revision-calendar-wrapper {
  grid-column: 1 / -1;
  width: 100%;
  box-sizing: border-box;
  margin: -7rem 0 0;
}

/* === Revision Calendar Container === */
.revision-calendar-container {
  width: 100%;
  background: rgba(86, 86, 86, 0);

  border-radius: 1rem;
  padding: 1rem;

}

/* === Week Grid and Day Boxes === */
.revision-week-grid {
  display: grid;
  grid-template-columns: repeat(7, 1fr);
  gap: 1rem;
  grid-auto-rows: minmax(220px, 1fr);
}
.revision-day-column {
  display: flex;
  flex-direction: column;
  background: rgba(255, 255, 255, 0.05);
  border-radius: 0.75rem;
  overflow: hidden;
}
.revision-day-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.75rem;
  background: rgba(86, 86, 86, 0.2);
  cursor: pointer;
}
.revision-day-name,
.revision-day-number {
  font-size: 1rem;
  font-weight: 600;
  color: #fff;
}
.revision-event-list {
  list-style: none;
  padding: 0.75rem;
  margin: 0;
  flex: 1;
  overflow-y: auto;
}
.revision-event-item {
  display: flex;
  align-items: baseline;
  gap: 0.5rem;
  margin-bottom: 0.5rem;
}
.revision-event-time {
  font-size: 0.9rem;
  color: rgba(188, 188, 188, 0.8);
  min-width: 6rem;
}
.revision-event-text {
  font-size: 1rem;
  color: #fff;
  flex: 1;
}

/* === Modal Styles === */
.revision-modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}
.revision-modal-content {
  background: rgba(86, 86, 86, 0.1);
  backdrop-filter: blur(10px);
  border-radius: 1rem;
  padding: 2rem;
  width: 90%;
  max-width: 400px;
  box-shadow: 0 4px 30px rgba(0, 0, 0, 0.2);
  color: #fff;
  display: flex;
  flex-direction: column;
  gap: 1rem;
}
.revision-modal-time-pickers {
  display: flex;
  align-items: center;
  gap: 0.5rem;
}
.revision-modal-input,
.revision-modal-time {
  width: 100%;
  padding: 0.75rem;
  border: none;
  border-radius: 0.75rem;
  outline: none;
  background: rgba(255, 255, 255, 0.1);
  color: #fff;
}
.revision-modal-actions {
  display: flex;
  justify-content: flex-end;
  gap: 1rem;
}
.revision-modal-btn {
  padding: 0.5rem 1rem;
  border: none;
  border-radius: 0.75rem;
  font-weight: 600;
  cursor: pointer;
}
.revision-modal-btn.save {
  background: rgba(255, 255, 255, 0.15);
  color: #fff;
}
.revision-modal-btn.cancel {
  background: rgba(255, 255, 255, 0.1);
  color: #f87171;
}
.revision-modal-btn.save:hover {
  background: rgba(255, 255, 255, 0.25);
}
.revision-modal-btn.cancel:hover {
  background: rgba(255, 255, 255, 0.2);
}
.todo-toggle-button {
  font-size: 1rem;
  font-weight: 600;
  color: #9a9a9a;
  background: rgba(86, 86, 86, 0.1);
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
  border: none;
  outline: none;
  border-radius: 1rem;
  padding: 0.75rem 1.5rem;
  box-shadow: 0 4px 30px rgba(0, 0, 0, 0.2);
  cursor: pointer;
  transition: background 0.2s ease-in-out, transform 0.2s ease;
}

.todo-toggle-button:hover {
  background: rgba(86, 86, 86, 0.2);
  transform: translateY(-2px);
}
.nav-button {
  font-size: 1rem;
  font-weight: 600;
  color: #9a9a9a;
  background: rgba(86, 86, 86, 0.1);
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
  border: none;
  outline: none;
  border-radius: 1rem;
  padding: 0.75rem 1.5rem;
  box-shadow: 0 4px 30px rgba(0, 0, 0, 0.2);
  cursor: pointer;
  transition: background 0.2s ease-in-out, transform 0.2s ease;
}

.nav-button:hover {
  background: rgba(86, 86, 86, 0.2);
  transform: translateY(-2px);
}
.revision-calendar-header {
  display: flex;
  align-items: center;
  justify-content: space-between; /* buttons at extremes */
  margin-bottom: 1rem;            /* tighten space under header */
  width: 100%;                    /* span full width of container */
  box-sizing: border-box;         /* include padding if any */
}

.revision-calendar-header h2 {
  margin: 0;
  flex: 1;           /* let the title grow and stay centered */
  text-align: center;
  font-size: 1.25rem;
  font-weight: 600;
}